<template>
  <horizontal>
    <b-overlay :show="showLoader" rounded="sm">
      <div class="row justify-content-center">
        <div class="col-md-8 col-lg-6 col-xl-5">
          <div class="card">
            <div class="card-body p-4">
              <div class="text-center w-75 m-auto">
                <div class="auth-logo">
                  <router-link to="/" class="logo logo-dark text-center">
                  <span class="logo-lg">
                    <img src="@/assets/images/logo.svg" alt height="22" />
                  </span>
                  </router-link>

                  <router-link to="/" class="logo logo-light text-center">
                  <span class="logo-lg">
                    <img src="@/assets/images/logo-light.png" alt height="22" />
                  </span>
                  </router-link>
                </div>
                <p class="text-muted mb-4 mt-3">
                  Enter new password
                </p>
              </div>

              <form @submit.prevent="ResetPassword" >
                <AlertMessage :variant="dangerSuccess" :message="responseMessage" v-if="responseMessage"/>
                <div class="form-group mb-3">
                  <label for="oldpassword">Enter current password</label>
                  <input
                      class="form-control"
                      type="password"
                      id="oldpassword"
                      placeholder="Enter current password"
                      v-model="oldpassword"
                      v-validate="'required'"
                      :class="{'is-invalid': vErrors.has('oldpassword') }"
                      name="oldpassword"
                  />
                  <span v-show="vErrors.has('oldpassword')" class="help text-danger">{{ vErrors.first('oldpassword') }}</span>
                </div>
                <div class="form-group mb-3">
                  <label for="password">Enter new password</label>
                  <input
                          class="form-control"
                          type="password"
                          id="password"
                          placeholder="Enter new password"
                          v-model="password"
                          v-validate="'required'"
                          :class="{'is-invalid': vErrors.has('password') }"
                          name="emailaddress"
                  />
                  <span v-show="vErrors.has('password')" class="help text-danger">{{ vErrors.first('password') }}</span>
                </div>
                <div class="form-group mb-3">
                  <label for="password">Enter new password</label>
                  <input
                      class="form-control"
                      ref="password"
                      type="password"
                      id="confirmpassword"
                      placeholder="Confirm password"
                      v-model="confirmpassword"
                      v-validate="'required|confirmed:password'"
                      :class="{'is-invalid': vErrors.has('confirmpassword') }"
                      name="emailaddress"
                  />
                  <span v-show="vErrors.has('confirmpassword')" class="help text-danger">{{ vErrors.first('confirmpassword') }}</span>
                </div>
                <div class="form-group mb-0 text-center">
                  <button class="btn btn-primary btn-block" id="bkcolor" type="submit">
                    Reset Password
                  </button>
                </div>
              </form>

            </div>
            <!-- end card-body -->
          </div>
          <!-- end card -->

          <!-- end row -->
        </div>
        <!-- end col -->
      </div>
    </b-overlay>
  </horizontal>
</template>

<script>
//import Auth from "./Auth";
//import Auth from  '../../components/register/Auth'
import horizontal from "../layouts/horizontal";
import constant from '../../components/constant/index';
import commonData from '../../protos/CommonData_grpc_web_pb';
import AlertMessage from "../../components/shared/AlertMessage";
import SweetAlertWrapper from "../../sweetAlert";
import SweetAlertConfig from "../../SwalConfig";

export default {
  data(){
    return{
      showLoader : false,
      dangerSuccess:'',
      responseMessage:'',
      oldpassword:'',
      password :'',
      confirmpassword:'',
      clients :{}
    }
  },
  components:{
    AlertMessage,
    horizontal
  } ,
  methods:{
    ResetPassword(){

      try {
        this.responseMessage = ''
        this.$validator.validateAll().then((result) => {
          if (result) {

            this.sweetAlert.showDialogYesOrNo(Object.assign(SweetAlertConfig, {
              title :' Reset password ?',
              confirmButtonText : 'Yes, reset'
            }), () => {

              this.responseMessage = '';
              let request = new commonData.CustomerPasswordResetInAppRequestMessage();
              request.setPasswordcurrent(this.oldpassword)
              request.setPasswordnew(this.password)
              request.setLanguageid(this.$store.getters.getUserInfo.languageId)

              this.showLoader = true;
              this.clients.resetUserPasswordInsideTheApp(request,this.$store.getters.metadata, (err, res) => {
                this.showLoader = false;
                if(err){
                  console.log("Error ", err);
                  this.dangerSuccess = 'danger';
                  this.responseMessage = constant.serverError;
                }
                else {
                  //loginErrorCode
                  if(!res.getIssuccess()){
                    this.dangerSuccess = 'danger';
                    this.responseMessage = res.getMessage();
                    // console.log('Email : ' , this.emailaddress);
                  }
                  else {
                    // start Save email in action
                    this.$store.dispatch('saveEmailAddress',{
                      emailAddress : this.emailaddress
                    })
                    this.sweetAlert.showMessage("Password reset",  res.getMessage())
                    // end
                    this.$router.push('/business/login').catch(() => { })
                  }
                }

              });

            })



            return;
          }
        });


      }catch (e) {
        console.log(" Error : ", e)
      }


    }
  },
  mounted() {
    console.log("mounted");
    this.clients = new commonData.CommonDataSvcClient(constant.URL,null,null);
  },
  created() {
    this.sweetAlert = new SweetAlertWrapper()
  }
}
</script>

<style scoped>

</style>

